import React from 'react';
import Abhimanyu from '../../Layouts/Home/Videos/Abimanyu.mp4';

const homeyashwantImage = () => {
    return <video
        className="video-fluid z-depth-1"
        loop
        controls
        muted
        preload="none"
    >
        <source src={Abhimanyu} type="video/mp4" />
    </video>
}
export default homeyashwantImage;